(function() {
  'use strict';

  angular
    .module('weilerDevChallenge', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.router', 'mm.foundation', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('weilerDevChallenge')
    .controller('ContactController', ContactController);

  /** @ngInject */
  function ContactController() {
    var contact = this;
    contact.weilerSrc = 'https://s3-us-west-2.amazonaws.com/weiler/assets/images/FB_IMG_1429572617234.jpg';
  }
})();

(function() {
  'use strict';

  angular
    .module('weilerDevChallenge')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1447543233091;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('weilerDevChallenge')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('weilerDevChallenge')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('contact', {
        url: '/contact',
        templateUrl: 'app/components/contact/contact.html',
        controller: 'ContactController',
        controllerAs: 'contact'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('weilerDevChallenge')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('weilerDevChallenge')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("weilerDevChallenge").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"row\"><div class=\"large-12 large-text-center columns\"><div class=\"panel\"><h1>\'Allo, \'Allo!</h1><img src=\"assets/images/yeoman.png\" alt=\"I\'m Yeoman\"><br><p>Always a pleasure scaffolding your apps.</p><p class=\"animated infinite\" ng-class=\"main.classAnimation\"><a href=\"#\" class=\"button success\" ng-click=\"main.showToastr()\">Splendid Toastr</a></p></div></div></div>");
$templateCache.put("app/components/contact/contact.html","<div class=\"row\"><img src=\"{{contact.weilerSrc}}\"></div>");}]);